<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <label>{{ $route.params.title }}</label>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="9">
          <v-btn text color="success" @click="savedata()">Save Changes</v-btn>
        <quill-editor
          ref="myQuillEditor"
          v-model="content"
          
        />
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import axios from 'axios'
export default {
    data(){
        return{
            content:''
        }
    },
    methods:{
        savedata(){
            let loading=this.$loading.show({})
            let form = new FormData();
            form.append('content',this.content)
            form.append('topic',this.$route.params.topic)
            axios.post('/admin/savecontent',{topic:this.$route.params.topic,content:this.content}).then(resp=>{
                loading.hide();
                let msg=resp.data.message;
                if(msg=='ok'){

                }else{
                    alert('faild to save');
                }
            }).catch(error=>{
                loading.hide();
                alert(error);
            })
        },
        getContent(){
            let loader=this.$loading.show({})
            let form=new FormData();
            form.append('topic',this.$route.params.topic);
            axios.post('/admin/getTopicContent',form).then(resp=>{
                loader.hide();
                this.content=resp.data.content
            }).catch(error=>{
                alert(error);
                loader.hide();
            })
        }
    },
    mounted(){
        this.getContent();
    }
}
</script>