<template>
    <v-container>
        <v-row>
            <v-col cols="12" md="10">
                <div class="heading red--text">Php how to demos</div>
            </v-col>
            <v-col cols="12" md="10">
                <v-row>
                    <v-col cols="12" sm="6" md="3" v-for="tutorial in tutList" :key='tutorial.id'>
                        <v-card router :to="{name:'tutorialContent',params:{id:tutorial.id,name:tutorial.name}}">
                            <v-card-text>
                                {{tutorial.name}}
                            </v-card-text>
                        </v-card>
                    </v-col>
                    <v-col cols="12" sm="6" md="3">
                        <v-btn text color="warning" @click="showdialog()">Add new Tutorial</v-btn>
                    </v-col>
                    
                </v-row>
            </v-col>
        </v-row>
        <v-dialog v-model="dialog" max-width="600" persistent>
            <v-card>
                <v-card-title></v-card-title>
                <v-card-text>
                    <v-text-field label="tutorial" v-model="tutorial"></v-text-field>
                     <v-btn text color="primary" @click='add_tutorial()'>Add</v-btn>
                <v-btn text color="red">Close</v-btn>
                </v-card-text>
               
            </v-card>
        </v-dialog>
    </v-container>
</template>
<script>
import axios from 'axios'
export default {
    data(){
        return{
            dialog:false,
            tutorial:'',
            tutList:''
        }
    },
   methods:{
       showdialog(){
           this.dialog=true;
       },
       add_tutorial(){
           let form= new FormData();
           form.append('tutorial',this.tutorial);
           let loader=this.$loading.show({})
           axios.post('/admin/addtutorial',form).then(resp=>{
               let msg=resp.data.message;
               loader.hide();
               if (msg=='ok') {
                   this.tutorial=''
                   this.dialog=false
                   this.getlist();
               }else if (msg=='fail') {
                   alert('failed to add new tutorial');
               }
           }).catch(error=>{
               loader.hide();
               alert(error);
           })

       },
       getlist(){
           let loader=this.$loading.show({})
           axios.post('/admin/getTutorials').then(resp=>{
               let tut=resp.data
               this.tutList=tut
               loader.hide();
           }).catch(error=>{
               alert(error);
               loader.hide();
           })
       }
   },
   created(){
       this.getlist();
   }
}
</script>