<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <label class="headline red--text">{{$route.params.tutorial_text}} {{ $route.params.title }}</label>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="9">
        <div class="output ql-snow">
          <div class="ql-editor" v-html="content"></div>
        </div>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="8">
        <v-card outlined>
          <v-card-text>
            <label class="subheading red--text">Tutubwire tutorial twagukorera ,Tanga igitekerezo/ikibazo</label>

            <div class="mt-3">
              <v-form @submit.prevent="validatecomment">
                <v-row>
                  <v-col cols="12" md="3">
                    <v-text-field dense label="Amazina" v-model="comment.names"></v-text-field>
                  </v-col>
                  <v-col cols="12" md="3">
                    <v-text-field dense label="Email" type="email" v-model="comment.email"></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <label>Igitekerezo</label>
                  </v-col>
                  <v-col cols="12">
                    <quill-editor ref="myQuillEditor" v-model="comment.comment" :options="editorOption" />
                    <v-btn type="submit" class="mt-1" color="primary">Send</v-btn>
                  </v-col>
                </v-row>
              </v-form>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      content: "",
      editorOption: {
        theme: "snow",
        modules: {
          toolbar: [
            ["bold"],
            ["code-block"],
            [{ color: [] }],
            ["link"],
            ["image"]
          ]
        }
      },
      comment:{
          names:'',
          email:'',
          comment:''
      }
    };
  },
  methods: {
    getContent() {
      let loader = this.$loading.show({});
      let form = new FormData();
      form.append("topic", this.$route.params.topic);
      axios
        .post("/demo/getTopicContent", form)
        .then(resp => {
          loader.hide();
          this.content = resp.data.content;
        })
        .catch(error => {
          alert(error);
          loader.hide();
        });
    },
    validatecomment(){
        let form = new FormData()
        let loader=this.$loading.show({})
        form.append('names',this.comment.names)
        form.append('email',this.comment.email)
        form.append('comment',this.comment.comment)
        form.append('topic',this.$route.params.topic)
        axios.post('/addComment',form).then(response=>{
            loader.hide()
            let msg=response.data.message
            if(msg=='ok'){
                alert('comment sent')
                this.comment.names=""
                this.comment.email=""
                this.comment.comment=""
            }
        }).catch(error=>{
            alert(error)
            loader.hide()
        })
    }
  },
  mounted() {
    this.getContent();
  }
};
</script>