<template>
    <v-container>
        <v-row>
            <v-col cols="12" md="10">
                <div class="heading red--text">{{$route.params.title}} Tutorial Demos</div>
            </v-col>
            <v-col cols="12" md="10">
                <v-list>
                    <v-list-item v-for="(topic,index ) in topicList" :key="index" :to="{name:'readcontent',params:{topic:topic.id,title:topic.topic,tutorial_id:topic_id,tutorial_text:topic_text}}">
                <v-list-item-title>
                    <label class="headline">{{topic.topic}}</label>
                </v-list-item-title>
              </v-list-item>
                   
                </v-list>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
import axios from 'axios'
export default {
    data(){
        return{
            topic:'',
            dialog:'',
            topicList:'',
            topic_text:'',
            topic_id:'',
            
            
        }
    },
    watch:{
        '$route'(x){
            this.gettopic();
            
        }
    },
    methods:{
       
    gettopic(){
        let loader=this.$loading.show({})
        let form =new FormData()
        this.topic_text=this.$route.params.title
        this.topic_id=this.$route.params.tutorial;
        form.append('tutorial',this.$route.params.tutorial);
           axios.post('/demo/getTopic',form).then(resp=>{
               let topic=resp.data
               this.topicList=topic
               
               loader.hide();
           }).catch(error=>{
               alert(error);
               loader.hide();
           })
    }

},
created(){
    this.gettopic();
   
}
}

</script>