<template>
  <div>
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <v-row>
      <v-col cols="12">
        <div class="output ql-snow">
          <div class="ql-editor" v-html="content"></div>
        </div>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-card-title>
            Text Editor
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="12" md="6">
                <editor
                  v-model="content1"
                  @init="editorInit"
                  lang="html"
                  theme="monokai"
                  height="300"
                ></editor>
                <v-btn color="success" class="mt-1" @click="saveoutput()"
                  >Save</v-btn
                >
              </v-col>
              <v-col cols="12" md="6" class="white">
                <div v-html="output" class="mytext"></div>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <v-card outlined>
          <v-card-text>
            <v-card-title>Comment</v-card-title>
            <label
              >Tanga cyangwa ubaze ikibazo
              <label class="red--text"
                >IGITEKEREZO KIGARAGARA IYO KIMAZE GUSUZUMWA</label
              ></label
            >

            <div class="mt-3">
              <v-form @submit.prevent="validatecomment">
                <v-row>
                  <v-col cols="12" md="3">
                    <v-text-field
                      dense
                      label="Amazina"
                      v-model.trim="$v.names.$model"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" md="3">
                    <v-text-field
                      dense
                      label="Email"
                      type="email"
                      v-model="email"
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12"><label>Igitekerezo</label></v-col>
                  <v-col cols="12">
                    <quill-editor
                      ref="myQuillEditor"
                      v-model="content2"
                      :options="editorOption"
                    />
                    <v-btn type="submit" class="mt-1" color="primary"
                      >Send</v-btn
                    >
                  </v-col>
                </v-row>
              </v-form>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from "axios";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
import { required, minLength } from "vuelidate/lib/validators";

export default {
  components: {
    Loading,
    editor: require("vue2-ace-editor"),
  },
  data() {
    return {
      lesson: "",
      content: "",
      content1: "",
      content2: "",
      output: "",
      fullPage: true,
      isLoading: true,
      submitStatus: null,
      comments: "",
      names: "",
      email: "",
      editorOption: {
        theme: "snow",
        modules: {
          toolbar: [
            ["bold", "italic", "underline"],
            ["code-block", "code"],
            [{ color: [] }, { background: [] }],
            ["link"],
            ["image"],
          ],
        },
      },
    };
  },
  validations: {
    names: {
      required,
      minLength: minLength(4),
    },
  },

  created() {
    let lesson = this.$route.params.lesson;
    this.lesson = lesson;
    let myform = new FormData();
    myform.append("lesson", lesson);
    axios
      .post("/lessonEditor", myform)
      .then((resp) => {
        this.content = resp.data.content;
        this.content1 = resp.data.file;
        this.output = resp.data.file;
        this.isLoading = false;
      })
      .catch((error) => {});
  },

  methods: {
    editorInit() {
      require("brace/ext/language_tools"); //language extension prerequsite...
      require("brace/mode/html");
      require("brace/mode/javascript"); //language
      require("brace/mode/less");
      require("brace/theme/monokai");
      require("brace/snippets/javascript"); //snippet
    },
    saveoutput() {
      this.output = this.content1;
    },
    comment() {},
    validatecomment() {
      alert();
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.submitStatus = "ERROR";
        elert();
      } else {
        // do your submit logic here
        this.submitStatus = "PENDING";
        setTimeout(() => {
          this.submitStatus = "OK";
        }, 500);
      }
    },
  },
};
</script>

<style scoped>
.mytext {
  font-weight: normal !important;
}
</style>
