<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <label>{{ $route.params.name }} Tutorial Demos</label>
        <v-spacer></v-spacer>
        <v-btn text color="red" @click="dialog = true">Add Topic</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <label class="red--text">Topic List</label>
        <v-row>
          <v-col cols="12" md="8">
              
            <v-list>
              <v-list-item v-for="(topic,index ) in topicList" :key="index" :to="{name:'addcontent',params:{topic:topic.id,title:topic.topic}}">
                <v-list-item-title>{{topic.topic}}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <v-dialog v-model="dialog" persistent max-width="500">
      <v-card>
        <v-card-title></v-card-title>
        <v-card-text>
          <v-text-field
            label="Topic"
            placeholder="Send  email with  php"
            v-model="topic"
          ></v-text-field>
          <v-btn text color="primary" @click="addtopic()">Add</v-btn>
          <v-btn text color="red" @click="dialog = false">close</v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import axios from 'axios'
export default {
    data(){
        return{
            topic:'',
            dialog:'',
            topicList:''
        }
    },
    methods:{
        addtopic(){
            let form=new FormData();
            form.append('topic',this.topic)
            form.append('tutorial',this.$route.params.id);
        let loader=this.$loading.show({})
        axios.post('/admin/addtopic',form).then(resp=>{

            const msg=resp.data.message;
            if(msg=='ok'){
                loader.hide();
                this.dialog=false
                this.gettopic();
            }else{
                alert('failed');
                loader.hide();
            }


        }).catch(error=>{
            alert(error);
            loader.hide();

        })

        
    },
    gettopic(){
        let loader=this.$loading.show({})
        let form =new FormData()
        form.append('tutorial',this.$route.params.id);
           axios.post('/admin/getTopic',form).then(resp=>{
               let topic=resp.data
               this.topicList=topic
               
               loader.hide();
           }).catch(error=>{
               alert(error);
               loader.hide();
           })
    }

},
mounted(){
    this.gettopic();
}
}

</script>
